/* 加载 */
export function showLoading(p){
  uni.showLoading({
    title: p || '加载中...',
    mask: true
  })
}
/* 轻提示 */
export function showToast(p, icon) {
  uni.showToast({
    title: p || '',
    mask: true,
    icon: icon || 'none',
	duration: 2500
  })
}
// 周
export function getWeekArray(week){
	let weeks=[];
	for(let i=1;i<=(week || 52);i++){
		weeks.push(i)
	}
	return weeks;
}
// 年
export function getYearsArray(year){
	let years=[];
	for (let i = 2024; i <= year; i++) {
		years.push(i);
	}
	return years;
}
// 月
export function getMonthArray(month){
	let months=[];
	for (let i = 1; i <= (month || 12); i++) {
		months.push(i)
	}
	return months;
}
// 季
export function getSeasonArray(season){
	let seasons=[];
	for(let i=1;i<=(season || 4);i++){
		seasons.push(i)
	}
	return seasons;
}
// 获取当前年
export function getYear(){
	const date = new Date();
	const year = date.getFullYear();
	const month = date.getMonth();
	return year
}
// 获取当前月
export function getMonth(){
	const date = new Date();
	const month = date.getMonth()+1;
	return month
}
// 获取当前日
export function getDay(){
	const date = new Date();
	const day = date.getDate();
	return day
}
/* 格式化日期 */
export function formatTime(data) {
  let date = new Date(data);
  let year = date.getFullYear();
  let month = date.getMonth() + 1;
  let day = date.getDate();
  return [year, month, day].map(formatNumber).join('-')
}

export function formatNumber(n) {
	n = n.toString();
	return n[1] ? n : '0' + n
}
// 获取本年第几周
export function getWeekNum(){
	let Y=getYear();
	let M=getMonth();
	let day=getDay();
	let date1 = new Date(Y, M - 1, day);
	let date2 = new Date(Y, 0, 1);
	let d = Math.round((date1.valueOf() - date2.valueOf()) / 86400000);
	return Math.ceil((d + ((date2.getDay() + 1) - 1)) / 7);
}
// 获取当前季度
export function getQuarter(){
	let M=getMonth();
	let quarter=0
	if(M<=3){
		return quarter=1;
	}
	if(M<=6){
		return quarter=2;
	}
	if(M<=9){
		return quarter=3;
	}
	if(M<=12){
		return quarter=4;
	}
}

// 获取url信息
export function getUrlValue() {
  var href=window.location.href;
  var value={};
  if(href.indexOf("?")!=-1){
    var strA = href.substr(href.indexOf("?")+1);
    if(strA.indexOf("#")!=-1){
      var end=strA.indexOf("#");
    }else{
      var end=strA.length;
    }
    var str=strA.substr(0,end).split("&");
    for(var i=0;i<str.length;i++){
      value[str[i].split('=')[0]] = str[i].split('=')[1];
    }
  }
  return value;
}

